import React, { Component } from 'react'
import styled from 'styled-components'
import { Container, Row, Col } from 'styled-bootstrap-grid'
import { getTeams } from 'api/api'
import { toJs } from 'utils/xml'

import Preview from './Preview'

const Label = styled.label`
  display: block;
  margin: 2em 0 0.5em;
`

const Input = styled.input`
  width: 100%;
  padding: 0.5em;
  outline: none;
`

const Separator = styled.div`
  height: 1px;
  background: #ccc;
  margin: 2em 0 1em;
`

const SelectorContainer = styled.div`
  display: flex;

  button {
    flex-shrink: 0;
    margin-left: 1em;
    margin-top: 1em;
  }
`

const Selector = styled.select`
  width: 100%;
  margin-top: 1em;
  height: 2em;
`


export default class PeriodStats extends Component {
  state = {
    selectedHomeTeam: '',
    selectedAwayTeam: '',
    home: {
      name: ''
    },
    away: {
      name: ''
    }
  }

  componentDidMount() {
    return getTeams().then(response => {
      const raw = toJs(response)
      const data = toJs(raw.NewDataSet.Table[Object.keys(raw.NewDataSet.Table)]._text)

      this.setState({teams: data.Hold})
      this.setState({loadingTeams: false})
    })
  }

  getStats() {
    
  }
  
  render() {
    const {
      teams,
      home,
      away,
      loadingTeams
    } = this.state

    console.log(this.state)

    return(
      <Container fluid>
        <Row>
        
          {/* Stats entry */}
          <Col col={6}>
            <Row>
              <Col>
                {loadingTeams ? (
                  <Label>Henter hold...</Label>
                ) : (
                  <Row>
                  
                    <Col col={6}>
                      <Label>Vlg hjemmehold</Label>
                      <SelectorContainer>
                        <Selector onChange={e => this.setState({selectedHomeTeam: e.target.value})}>
                          <option value={null} key={0}>---</option>
                          {teams && teams.map(team => {
                            return(
                              <option
                                value={team._attributes.TeamID}
                                key={team._attributes.TeamID}>
                                {team._attributes.TeamName}
                              </option>
                            )
                          })} 
                        </Selector>
                      </SelectorContainer>
                    </Col>
                    
                    <Col col={6}>
                      <Label>Vlg udehold</Label>
                      <SelectorContainer>
                        <Selector onChange={e => this.setState({selectedAwayTeam: e.target.value})}>
                          <option value={null} key={0}>---</option>
                          {teams && teams.map(team => {
                            return(
                              <option
                                value={team._attributes.TeamID}
                                key={team._attributes.TeamID}>
                                {team._attributes.TeamName}
                              </option>
                            )
                          })} 
                        </Selector>
                      </SelectorContainer>
                    </Col>

                    <Col col={12}>
                      <button style={{ marginTop: '2em' }} onClick={e => this.getStats()}>Hent stats</button>
                    </Col>
                  
                  </Row>

                )}
                
                <Separator />

                <Row>
                <Col col={6}>
                    <Label>Hjemmehold</Label>
                    <Input
                      type="text"
                      value={home.name}
                      onChange={
                        e => this.setState({
                          home: {
                            ...home,
                            name: e.target.value
                          }
                        })
                      }
                    />
                  </Col>
                  <Col col={6}>
                    <Label>Udehold</Label>
                    <Input
                      type="text"
                      value={away.name}
                      onChange={
                        e => this.setState({
                          away: {
                            ...away,
                            name: e.target.value
                          }
                        })
                      }
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>

          {/* End stats entry */}

          {/* Preview + output */}
          {/*
          <Col col={6}>
            <Row>
              <Col>
                <Preview
                  period={period}
                  home={home}
                  away={away}
                />
              </Col>
            </Row>
          </Col>
          */}
          {/* End preview */}

        </Row>
      </Container>
    )
  }
}
